.offers {
  overflow: hidden;
  padding-bottom: 24px;
}
.offers .main-offers {
  margin-top: 0;
  padding-top: 76px;
}
.offers .main-offers .image-wrapper {
  position: relative;
  max-width: 350px;
  margin-left: 48px;
}
.offers .main-offers .image-wrapper svg {
  position: absolute;
}
.offers .main-offers .image-wrapper svg:first-of-type {
  top: 10%;
  inset-inline-start: -20%;
}
.offers .main-offers .image-wrapper svg:nth-of-type(2) {
  bottom: 20%;
  inset-inline-start: -100%;
}
.offers .main-offers .image-wrapper .item {
  display: flex;
  align-items: baseline;
  gap: 12px;
  max-width: 300px;
  padding-inline: 12px;
  position: absolute;
  bottom: 20%;
  inset-inline-end: -20%;
  font-size: var(--title-font-normal);
  background-color: var(--background-color);
  border-radius: 12px;
  box-shadow: 0px 0px 12px 0px rgba(91, 91, 91, 0.5098039216);
}
.offers .main-offers .image-wrapper .item span svg {
  position: static;
}
.offers .main-offers .content-wrapper .section-title {
  font-size: var(--title-font-lagre);
}
.offers .main-offers .content-wrapper .form-search {
  max-width: 370px;
  margin-top: 40px;
}
.offers .main-offers .content-wrapper .form-search .input-group {
  position: relative;
}
.offers .main-offers .content-wrapper .form-search .input-group svg {
  position: absolute;
  transform: translate(-50%, -50%);
  inset-inline-start: 0;
  top: 50%;
}
.offers .main-offers .content-wrapper .form-search .input-group input {
  padding: 12px;
  width: 100%;
  border-radius: 12px;
  border: 1px solid var(--background-color);
  background-color: #f8f8f8;
  box-shadow: 0px 1px 8px 0px rgba(47, 47, 47, 0.1607843137);
  outline: none;
  text-indent: 36px;
}
.offers .main-offers .content-wrapper .form-search .input-group input:focus {
  border: 1px solid #ccc;
}
.offers .main-offers .content-wrapper .form-search .input-group input:active {
  border: 1px solid #ccc;
}
.offers .offers-container {
  padding-inline: 4%;
}
.offers .offers-container .offers-content {
  margin-top: 76px;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 48px;
}
.offers .offers-container .offers-content .offers-card {
  padding: 24px;
  box-shadow: 0px 0px 12px 0px rgba(61, 61, 61, 0.0745098039);
  border-radius: 12px;
}
.offers .offers-container .offers-content .offers-card .photo {
  width: 100%;
}
.offers .offers-container .offers-content .offers-card .photo img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 12px;
}
.offers .offers-container .offers-content .offers-card .offers-card-title {
  font-size: clamp(16px, 1.5vw, 20px);
}
.offers .offers-container .offers-content .offers-card .offers-card-descrption {
  font-size: clamp(14px, 1.2vw, 18px);
  line-height: 1.5;
  font-weight: 500;
  color: #3d3d3d;
}

@media screen and (max-width: 1900px) {
  .offers .offers-container .offers-content {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (max-width: 1400px) {
  .offers .offers-container .offers-content {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 900px) {
  .offers .offers-container .offers-content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 600px) {
  .offers .offers-container .offers-content {
    grid-template-columns: repeat(1, 1fr);
  }
}/*# sourceMappingURL=Offers.css.map */